@import "typography";
@import "colours";
@import "header";
@import "footer";
@import "buttons";
@import "blocks/blocks";

body {
    isolation: isolate;
    background-color: var(--col-light);
}

a {
    color: var(--col-green-400);
}

a,
a.noline {
    text-decoration: none;
}
a.anchor {
    display: block;
    position: relative;
    top: -150px;
    visibility: hidden;
}

@media (min-width:992px) {
    .cols-lg-2 {
        columns: 2;
    }
    .cols-lg-3 {
        columns: 3;
    }
}

ul {
    padding-inline-start: 1.5rem;
    li {
        line-height: 2;
    }
}

ul li ul {
    list-style-type: '- ';
}

.container-xl .container-xl {
    padding-inline: 0;
}


